.sidebar {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top:0;
  left: 0;
  /* background-color: #111; */
  background: url(/bs-group-website/static/media/Home_bg.f0f267fc.webp);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
  z-index: 1000;
}

.sidebar a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;
  transition: 0.3s;
}

.sidebar a:hover {
  color: #0090f8;
}

.sidebar .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 50px;
  margin-left: 50px;
  transition: 0.5s;
  border: none;
  border-style: none;
  outline-color: none;
  outline: none;
  background-color: transparent;
  color: #f6f6f6;
}
.closebtn:hover{
  color: #0090f8;
} 

.openbtn {
  font-size: 20px;
  cursor: pointer;
  background-color: #444;
  color: white;
  padding: 10px 15px;
  border-radius: 50%;
  border: none;
  position: fixed;
  z-index: 999;
top: 10px;
right:40px;
}

.openbtn:hover {
  background-color: #111;
  color: #0090f8;
}

#main {
  transition: margin-left .5s;
  padding-right: 16px;
}

/* On smaller screens, where height is less than 450px, change the style of the sidenav (less padding and a smaller font size) */
@media screen and (max-height: 450px) {
  .sidebar {padding-top: 15px;}
  .sidebar a {font-size: 18px;}
  

}





