  /* button css */
  
  .button {
    position: relative;
    padding: .8em 1.5em;
    border: none;
    background-color:rgba(19, 7, 7, 0.705);
    cursor: pointer;
    outline: none;
    font-size: 1.3rem;
    margin: 2.5em 0.8em;
    letter-spacing: 2.5px;
    border-top-right-radius: 20px;
    border-bottom-left-radius: 20px;
    transition: 0.6s;
    
}
.button:hover{
    border-top-right-radius: 0px;
    border-bottom-left-radius: 0px;
  
}

.button.effect {
    color: #add8e6;
  
}
.button.effect:hover {
    color: #e4f8ff;
    
}

.button.effect.effect::after,
.button.effect.effect::before {
    content: "";
    display: block;
    position: absolute;
    width: 25%;
    height: 25%;
    border: 2px solid;
    transition: all 0.6s ease;
    border-radius: 2px;
}

.button.effect.effect::after {
    bottom: 0;
    right: 0;
    border-top-color: transparent;
    border-left-color: transparent;
    border-bottom-color: #add8e6;
    border-right-color: #add8e6;
}

.button.effect.effect::before {
    top: 0;
    left: 0;
    border-bottom-color: transparent;
    border-right-color: transparent;
    border-top-color: #add8e6;
    border-left-color: #add8e6;
}

.button.effect.effect:hover:after,
.button.effect.effect:hover:before {
    width: 105%;
    height: 105%;
   
}
