/* up arrow scroll  */
body{
  font-family: "Roboto", sans-serif !important;
}
.Up_Arrow {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: rgba(155, 155, 155, 0.637);
  position: fixed;
  right: 40px;
  top: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: 0.5s;
  cursor: pointer;
}
.Up_Arrow:hover {
  background-color: #0090f8;
}
.ScrollArrow_show {
  opacity: 1;
}
.up_arrow_color {
  color: #0090f8;
}
.Up_Arrow:hover .up_arrow_color {
  color: #000;
}


